.Winner {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    padding: 10px;
    width: 200px;
    border: 1px solid #008000;
    border-radius: 10px;
    background-color: #008000;
    color: #fff;
    font-size: 20px;
    transition: .4s;
}